import {
  PaymentFormFieldHooks,
  PaymentFormField,
  MultiPaymentHooks,
  MultiPaymentHooksConfig,
  StepperConfig,
  StepperAction,
} from '@backbase/payment-orders-ang';
import { FormControl } from '@angular/forms';

export function triggerHook(
  name: PaymentFormFieldHooks,
  component: PaymentFormField
) {
  const control = <FormControl>component.group.get(component.config.name);
  const hook = component.config.hooks && component.config.hooks[name];
  if (typeof hook === 'function') {
    // Triggering the hook with component specific parameters
    hook({
      component, // The component instance
      control, // The component form field
      group: component.group, // The parent form group
    });
  }
}

export function triggerStepperHook(
  hooks: MultiPaymentHooks,
  hookName: MultiPaymentHooksConfig | undefined,
  callback?: () => void,
  stepperConfig?: StepperConfig,
  action?: StepperAction
) {
  const hook = hookName && hookName[hooks];
  const doneFn = callback as () => void;

  if (typeof hook === 'function') {
    if (hooks === MultiPaymentHooks.onClick) {
      hook({
        doneFn, // Callback function that is triggered when hooks complete
        stepperConfig,
        action,
      });
    } else {
      hook({});
    }
  }
}
